* {
    box-sizing: border-box;
}

::selection {
    background-color: $clr1;
    color: $clr6;
}

html {
    font-family: livvic;
    color: $clr2;
}

body {
    font-size: rem(16px);
    font-family: livvic;
    margin: 0 !important;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: livvic-semi-bold;
    margin: 0 0 0.6em;
    margin: 0;
    color: $clr1;
    font-weight: 400;
}

img {
    width: 100%;
    height: 100%;
}

p {
    color: $clr1;
    margin: 0;
    font-weight: 400;
}

a {
    text-decoration: none;
}
a:hover {
    text-decoration: none;
}
//hides arrows in input type number
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}
