.results-dev-container {
    width: 100%;
    background-color: $clr2;
    color: $clr1;
    font-family: livvic;
    .bg-style {
        z-index: 0;
        width: 67px;
        height: 56px;
        position: relative;
        transform: translateY(10px);
        z-index: 0;
        img {
            height: 100%;
            width: 100%;
            object-fit: none;
        }
    }
    .results-dev {
        font-size: 40px;
        text-align: center;
        span {
            color: #79c8c7;
        }
    }
    .res-list-container {
        margin-top: 88px;
        .users-desc-card {
            width: 350px;
            text-align: center;
            margin-bottom: 48px;
            .desc {
                z-index: 5;
                height: 95px;
                font-family: livvic-semi-bold;
            }
            .user-details {
                height: 46px;
                margin-bottom: 16px;
                h3 {
                    font-size: 18px;
                    line-height: 28px;
                    color: #79c8c7;
                    font-family: livvic-bold;
                }
                h4 {
                    font-size: 14px;
                    font-weight: 300;
                    font-family: livvic-med-italic;
                }
            }
            .user-img {
                width: 62px;
                height: 62px;
                border: 2px solid #c4fffe;
                border-radius: 50%;
                overflow: hidden;
            }
        }
    }
    @include for-narrow-layout {
        height: 745px;
        .results-dev {
            // width: 932px;
            height: 100px;
            font-size: 40px;
            padding: 0 90px;
            margin-top: 140px;
        }
        .res-list-container {
            margin-top: 88px;
            flex-direction: row;
            align-items: flex-start;
            .users-desc-card {
                width: 350px;
                margin: 0;
                .desc {
                    margin-bottom: 28px;
                }
                .user-details {
                    height: 46px;
                    margin-bottom: 32px;
                }
                // .user-img {
                //     width: 62px;
                //     height: 62px;
                //     border: 2px solid #c4fffe;
                //     border-radius: 50%;
                //     overflow: hidden;
                // }
            }
        }
    }
}
