.site-info-container {
    background-color: $clr3;
    .site-info {
        margin-top: 100px;
        .site-info-title {
            width: 90%;
            font-family: livvic-Bold;
            font-size: 48px;
            font-weight: 700;
            line-height: 48px;
        }
        .site-info-card {
            width: 90%;
            height: 110px;
            .info-card-logo {
                width: 56px;
                height: 56px;
            }
            .info-card-desc {
                width: 100%;
                gap: 16px;
                h3 {
                    height: 28px;
                    color: $clr5;
                    font-family: livvic-Bold;
                    font-size: 18px;
                    font-weight: 700;
                    line-height: 28px;
                }
                p {
                    height: 47px;
                    font-family: livvic-semi-bold;
                    font-size: 1rem;
                    line-height: 25px;
                }
            }
        }
    }
    @include for-narrow-layout {
        height: 684px;
        .site-info {
            margin-top: 200px;
            flex-direction: row;
            .site-info-title {
                width: 445px;
                height: 145px;
                font-family: livvic-Bold;
                font-size: 48px;
                font-weight: 700;
                line-height: 48px;
            }
            .site-info-card {
                width: 525px;
                height: 110px;

                .info-card-logo {
                    width: 56px;
                    height: 56px;
                }
                .info-card-desc {
                    width: 445px;
                    gap: 16px;
                    h3 {
                        height: 28px;
                        color: $clr5;
                        font-family: livvic-Bold;
                        font-size: 18px;
                        font-weight: 700;
                        line-height: 28px;
                    }
                    p {
                        height: 47px;
                        font-family: livvic-semi-bold;
                        font-size: 1rem;
                        line-height: 25px;
                    }
                }
            }
        }
    }
}
