header {
    background-color: $clr2;
    height: 700px;
    .app-header-container {
        width: 100%;
        height: 120px;
        margin-bottom: 140px;
        padding-top: 77px;
        .app-header {
            color: $clr1;
            .logo {
                width: 160px;
                height: 40px;
                font-size: 40px;
                font-family: inter-bold;
                margin-inline-end: 80px;
            }
            .header-links {
                width: 140px;
                align-items: flex-end;
                font-family: livvic;
                p {
                    width: 50px;
                    height: 28px;
                }
            }
            .contact-btn {
                width: 153px;
                height: 48px;
                border-radius: 24px;
                border: 2px solid #ffffff;
                display: flex;
                align-items: center;
                justify-content: center;
                p {
                    width: 88px;
                    height: 28px;
                    font-family: livvic-semi-bold;
                    font-size: 18px;
                    font-weight: 600;
                    line-height: 28px;
                }
            }
        }
    }

    .hero-container {
        flex-direction: column;
        .hero-title {
            width: 635px;
            height: 200px;
            font-family: "Livvic - Bold";
            font-size: 70px;
            font-weight: 700;
            line-height: 100px;
            text-align: left;
            span {
                color: #f67e7e;
            }
        }
        .hero-desc {
            width: 445px;
            height: 109px;
            font-family: livvic-semi-bold;
            font-size: 18px;
            font-weight: 600;
            line-height: 28px;
        }
        @include for-narrow-layout {
            flex-direction: row;
        }
    }
    .hero-icon-container {
        width: 400px;
        position: relative;
        top: 125px;
        left: 70%;
    }
}
