.contact-us-area {
    background-color: $clr5;
    .contact-icon-container {
        width: 200px;
        position: relative;
        right: 60px;
        top: 5px;
    }
    .contact-section-container {
        .contact-section {
            height: 200px;
            .contact-title {
                h2 {
                    color: $clr3;
                    height: 53px;
                    font-family: livvic-bold;
                    font-size: 48px;
                    line-height: 48px;
                }
            }
            .contact-btn {
                width: 153px;
                height: 48px;
                border-radius: 24px;
                border: 2px solid $clr3;
                display: flex;
                align-items: center;
                justify-content: center;
                p {
                    color: $clr3;
                    width: 88px;
                    height: 28px;
                    font-family: livvic-semi-bold;
                    font-size: 18px;
                    font-weight: 600;
                    line-height: 28px;
                }
            }
        }
    }
}
